import React, { Component } from 'react'
import { withRouter } from 'react-router-dom'
import Welcome from '../Header/Welcome'
import { signUp, signIn } from '../../api/auth'
import { signUpSuccess, signUpFailure } from '../AutoDismissAlert/messages'

import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

class SignUp extends Component {
  constructor (props) {
    super(props)

    this.state = {
      email: '',
      password: '',
      passwordConfirmation: ''
    }
  }

handleChange = (event) =>
  this.setState({
    [event.target.name]: event.target.value
  })

onSignUp = (event) => {
  event.preventDefault()

  const { msgAlert, history, setUser } = this.props

  signUp(this.state)
    .then(() => signIn(this.state))
    .then((res) => setUser(res.data.user))
    .then(() =>
      msgAlert({
        heading: 'Sign Up Success',
        message: signUpSuccess,
        variant: 'success'
      })
    )
    .then(() => history.push('/'))
    .catch((error) => {
      this.setState({ email: '', password: '', passwordConfirmation: '' })
      msgAlert({
        heading: 'Sign Up Failed with error: ' + error.message,
        message: signUpFailure,
        variant: 'danger'
      })
    })
}

render () {
  const { email, password, passwordConfirmation } = this.state

  return (
    <>
      <div className='row'>
        <div className='col-sm-10 col-md-8 mx-auto mt-5 pt-5'>
          <Form onSubmit={this.onSignUp}>
            <h3>Register</h3>
            <Form.Group controlId='email'>
              <Form.Control
                className='mb-3 mt-4 border border-dark rounded-0 p-2'
                required
                type='email'
                name='email'
                value={email}
                placeholder='Enter email'
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group controlId='password'>
              <Form.Control
                className='mb-3 border border-dark rounded-0 p-2 '
                required
                name='password'
                value={password}
                type='password'
                placeholder='Password'
                onChange={this.handleChange}
              />
            </Form.Group>
            <Form.Group controlId='passwordConfirmation'>
              <Form.Control
                className='mb-3 border border-dark rounded-0 p-2'
                required
                name='passwordConfirmation'
                value={passwordConfirmation}
                type='password'
                placeholder='Confirm Password'
                onChange={this.handleChange}
              />
            </Form.Group>
            <div className='d-grid gap-2 rounded-3'>
              <Button className='p-2' variant='dark' type='submit'>
Submit
              </Button>
            </div>
          </Form>
        </div>
      </div>
      <Welcome />
    </>
  )
}
}

export default withRouter(SignUp)
